import {api} from '/js/api'

api({
	types: 'meinfotype/GetMediaExhibtionInfoType?type=1'
});


api.types(data => {
	let html = '';
	data.forEach(v=>{
		html += `<li><b class="padRed"></b> <a href="./list.html?id=${v.id}">${v.name}</a></li>`;
	});
	document.getElementById('ulBuyerList').innerHTML = html;
});